<template>
  <el-container class="main-container">
    <side-bar></side-bar>
    <el-container class="main-wrapper" direction="vertical">
       <page-header></page-header>
      <el-main direction="horizontal">
        <router-view />
      </el-main>
      <el-footer>
        <span>©{{ date }} ech0o </span>
        <span><a href="https://github.com/ech0o"><font-awesome-icon :icon="['fab','github']" size="lg"></font-awesome-icon></a></span>
      </el-footer>
    </el-container>
    
  </el-container>
</template>
<script>
import SideBar from "./SideBar";
import store from "@/store/index";
import pageHeader from "./pageHeader"
export default {
  name: "Layout",
  components: { SideBar, pageHeader},
  computed:{
    date:function(){
      return new Date().getFullYear()
    }
  },
  data() {
    return {
      title: this.$route.path,
      user: store.getters.name,
      isCollapse:false,
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    }
  }
};
</script>
<style lang="scss" scoped>

.el-main{
  padding-top:10px;
}

.main-container{
  height: 100%;
}

.main-wrapper{
  min-height: 100vh;
}

.el-footer{
  text-align: center;
  line-height: 60px;
  background-color: rgba(90, 100, 101, 0.83);
  color: white;
  a{
    text-decoration: none;
    color:white;
  }
}
</style>