<template>
  <el-header>
    <el-button size="mini" @click="changeCollapse" class="sidebar-toggle">
      <font-awesome-icon :icon="['fas','bars']" size="lg">test</font-awesome-icon>
    </el-button>
    <!-- <span>{{ this.$route.meta.title }}</span> -->
    <el-dropdown @command="handleCommand">
      <span class="el-dropdown-link">
        {{ user }}
        <i class="el-icon-arrow-down el-icon--right"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item command="a">
          退出登录
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </el-header>
</template>
<script>
import store from "@/store/index";

export default {
  name: "pageHeader",
  data() {
    return {
      user: store.getters.name,
      isCollapse: store.getters.sidebar
    };
  },

  methods: {
    logout() {
      this.$store.dispatch("user/logout");
      this.$router.push({ path: "/login" });
    },
    handleCommand(command){
      if(command==='a'){
        this.logout()
      }
    },
    changeCollapse() {
      if (store.getters.sidebar === true) {
        this.$store.dispatch("sidebar/closeSidebar");
      } else {
        this.$store.dispatch("sidebar/openSidebar");
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.el-header {
  background-color: #fff;
  box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
  font-size: 16px;
  line-height: 60px;
  .sidebar-toggle{
    margin-right: 15px;
  }
  
}
.el-dropdown {
  float: right;
  font-size: 16px;
}
</style>