import { getAllGrade } from '@/api/grade'

const state = {
	classes: []
}

const mutations = {
	SET_CLASSES(state, classes) {
		state.classes = classes
	},
	CHANGE_CLASSES(state, data) {
		for (const v of state.classes) {
			if (v.id === data.id) {
				const index = state.classes.indexOf(data)
				state.classes.splice(index, 1, data)
				break
			}
		}
	},
	DELETE_CLASS(state, data) {
		for (const v of state.classes) {
			if (v.id === data.id) {
				const index = state.classes.indexOf(data)
				state.classes.splice(index, 1)
				break
			}
		}
	}
}

const actions = {
	getClasses({ commit }) {
		return new Promise((resolve, reject) => {
			getAllGrade().then(resp => {
				commit('SET_CLASSES', resp.data)
				resolve(resp.data)
			}).catch(error => {
				reject(error)
				throw new Error(error)
			})
		})
	},
	setClasses({ commit }, classes) {
		commit('SET_CLASSES', classes)
	},

	editClasses({ commit }, cls) {
		commit('CHANGE_CLASSES', cls)
	},
	deleteClasses({ commit }, cls) {
		commit('DELETE_CLASS', cls)
	}


}


export default {
	namespaced: true,
	state,
	mutations,
	actions
}