import service from '@/plugin/axios/service'


export function login(params) {
    return service({
        url: 'api/auth/login',
        method: 'post',
        params: params
    })
}

export function getInfo(token) {
    return service({
        url: 'api/user/info',
        method: 'post',
        params: token
    })
}


