import { getToken } from './util/auth'
import router from './router'
import store from './store/index'

router.beforeEach(async (to, from, next) => {


    document.title = (to.meta.title + ' - school admin') || 'school admin'
    const token = getToken()
    if (token) {
        if (to.path === '/login') {
            next('/')
        } else {
            const hasRoles = store.getters.roles && store.getters.roles.length > 0
            if (hasRoles) {
                next()
            } else {
                try {
                    const { roles } = await store.dispatch('user/getInfo', getToken())
                    const routes = await store.dispatch('permission/generateRoutes', roles)
                    router.addRoutes(routes)
                    store.dispatch('grade/getClasses')
                    next({ ...to, replace: true })
                } catch (error) {
                    throw new Error(error)
                    //next('/login')
                }
            }

        }

    } else {
        if (to.path === '/login') {
            next()
        } else {
            next('/login')
        }
    }




})

