import service from '@/plugin/axios/service'

export function getAllGrade(){
    return service({
        url:'/api/grade/all',
        method:'get'
    })
}

export function getGradeByPage(page,size){
    return service({
        url:'/api/grade/page',
        params:{page:page,size:size}
    })
}

export function getGrade(params) {
    return service({
        url:'/api/grade/'+params,
        method:'get'
    })
}

export function createGrade(params){
    return service({
        url:'/api/grade/',
        method:'post',
        data:params
    })
}

export function updateGrade(params){
    return service({
        url:'/api/grade/',
        method:'put',
        data:params
    })
}

export function deleteGrade(id){
    return service({
        url:'/api/grade/'+id,
        method:'delete'
    })
}
