import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/components/layout/Layout'



const index = () => import('@/components/index')
const Login = () => import('@/components/Login')
const StudentManage = () => import('@/components/StudentManage')
const userManage = () => import('@/components/UserManage')
const gradeManage = ()=>import('@/components/GradeManage')
const err = () =>import('@/views/404')
const roleManage = ()=> import('@/views/RoleManage')

Vue.use(Router)

export const constantRoutes = [
  {
    path: "/login",
    name: "Login",
    hidden: true,
    component: Login,
    meta:{
      title:'login'
    }
  },
  {
    path: '/',
    name: 'index',
    redirect: '/dashboard',
    component: Layout,
    meta:{
      title:'主页',
      icon:['fas','home']
    },
    children: [

      {
        path: 'dashboard',
        name: 'userIndex',
        component: index,
        hidden:true,
        meta:{
          title:'dashboard'
        }
      }
    ]
  },

]

export const asyncRoutes = [

  {
    path: '/manage',
    name: 'manage',
    component: Layout,
    alwaysShow:true,
    redirect: '/student',
    meta: {
      roles: ['ROLE_ADMIN', 'ROLE_USER'],
      title: '管理',
      icon: ['fas','tools']
    },
    children: [

      
      {
        path: 'class/:clazz?',
        name: 'student',
        component: StudentManage,
        meta: {
          roles: ['ROLE_ADMIN', 'ROLE_USER'],
          title: '学生管理',
          icon: ['fas','user-graduate']
        }
      },
      {
        path: 'user',
        name: 'user',
        component: userManage,
        meta: {
          title:'用户管理',
          roles: ['ROLE_ADMIN'],
          icon:['fas','user']
        }
      },
      {
        path: 'role',
        name: 'role',
        component: roleManage,
        meta: {
          title:'角色管理',
          roles: ['ROLE_ADMIN'],
          icon:['fas','tasks']
        }
      },
      {
        path:'grade',
        name:'class',
        component:gradeManage,
        meta:{
          title:'班级管理',
          roles:['ROLE_ADMIN'],
          icon:['fas','users']
        }
      }
    ]

  },
  {
    path:'*',
    name:'404',
    hidden:true,
    component:err,
    meta:{
      title:'404',
      roles:['ROLE_ADMIN','ROLE_USER']
    }
  }

]

const createRouter = () => new Router({
  routes: constantRoutes
})

const router = createRouter()

export function resetRouter(){
  const newRouter = createRouter()
  router.matcher = newRouter.matcher
}

export default router

